@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;0,800;1,300;1,400&display=swap);
body,
html {
	height: 100%;
}

#page-container {
	position: relative;
	min-height: 100vh;
	padding-bottom: 2.4rem;
}

#footer {
	width: 100%;
	margin-top: -37px;
	/* Footer height */
}

body {
	margin: 0;
	font-family: "JetBrains Mono", monospace;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App {
	font-family: "JetBrains Mono", monospace;
	padding-top: 5vh;
	padding-left: 10px;
	padding-right: 10px;
	padding-bottom: 3vh;
}

@media screen and (min-width: 768px) {
	.App {
		padding-left: 10%;
		padding-right: 10%;
	}
}

body {
	margin: 0px;
	padding: 0px;
	background-color: #fcfcfc;
	overflow-x: hidden;
	max-width: 100vw;
}

.error-marker {
	position: absolute;
	background-color: red;
	opacity: 0.3;
	color: red;
}

#themeBtn {
	-webkit-animation: rotate 5s linear infinite;
	        animation: rotate 5s linear infinite;
}

@-webkit-keyframes rotate {
	0% {
		transform: rotate(0deg);
	}

	20% {
		transform: rotate(360deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

@keyframes rotate {
	0% {
		transform: rotate(0deg);
	}

	20% {
		transform: rotate(360deg);
	}

	100% {
		transform: rotate(360deg);
	}
}
.saved {
	overflow: hidden;
	transition: all 0.3s ease-in-out;
	border-radius: 3px;
	color: black;
	background-color: transparent;
}
.saved-visible {
	overflow: hidden;
	transition: all 0.3s ease-in-out;
	border-radius: 3px;
	color: black;
	opacity: 0.7;
	background-color: transparent;
}

